.form-btn {
    display: block;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 40px;
    background: $primaryBlack;
    border: 1px solid $accentColor;
    border-radius: 2px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    color: $primaryWhite;
    text-transform: uppercase;
    border: none;
    outline: none;
    cursor: pointer;
    transition: background 250ms linear, color 250ms linear;

    &:hover,
    &:focus {
        background-color: $accentColor;
        color: $primaryBlack;
    }
}

.button-menu {
  display: block;
  margin-top: 35px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 24px 14px;

  font-family: inherit;
  font-weight: 700;
  line-height: 20px;

  cursor: pointer;
  color: $accentColor;
  background: $primaryBlack;

  border: 2px solid $bgrColorLemon;
  border-radius: 25px;

  @include for-tablet-landscape-up {
    margin-top: 31px;
    margin-right: 0;
    border: 2px solid $bgrColorDarkLemon;
  }
}
